:root{
    --main-color: #3d0c11
}

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;

}

body{
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 140px 1fr; 
    height: 100vh;
    width: 100vw;
}

header{
    align-items: center;
    width: 100%;
    height: 140px;
    background-color: var(--main-color) ;
    grid-column:1 / 3; 
    display: grid;
    grid-template-columns: 150px 2fr;
    justify-items: center;
}


.boxAvatar{
    width: 100px;
    height: 100px;
    overflow: hidden;
    border: none;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    border: 3px white solid;
}

#avatar{
    width: 200px;
    height: auto;
}


.headerTitle{
    color: white;
    font-size: 3.5rem;
    font-weight: 800;
    font-family: Helvetica, sans-serif;
}

/*  aside  */

#aside{
    width: 150px;
    height: auto;
    border-right: 1px solid var(--main-color);
    grid-row: 2 / 3;
    display: flex;
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
    flex-direction: column;
    align-items: left;
}

#asideProjectList{
    border: solid 1px black;
    width: 100%;
    height: 60px;
    display: flex;
    
}

#divCreateProject{
    align-self: flex-end;
    padding: 5px;
}


.btnCreateProject{
    background-color: var(--main-color);
    color: white;
    font-weight: 700;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid white;
}
